import React from "react"
class Endscreen extends(React.Component){
    //This gives the endscreen. It will also export the data here.
    // It takes in data (array of objects) with all the trial data in it, 
    //bonus (int) which is the total money earned, and money which is the total 
    //in game dollars accumulated. It is a child component of "Horizon Game"
    constructor(props){
        super(props)
    }
    render(){
        let query_obj = Object.fromEntries([...new URLSearchParams(window.location.search)])
        let query_str = `?id=${query_obj.id}&sona_id=${query_obj.sona_id}&temporal_first=${query_obj.temporal_first}`

        let attention_link = "https://uarizona.co1.qualtrics.com/jfe/form/SV_b17JD0qnp4q1yf4"
        let temporal_link = "https://uarizona.co1.qualtrics.com/jfe/form/SV_6FIDMxFV9BfYdQa"
        console.log(query_obj.temporal_first)
        if (query_obj.temporal_first == "true"){
            setTimeout(()=>{window.location.href=attention_link + query_str},1500)
        }
        else{
            setTimeout(()=>{window.location.href=temporal_link + query_str},1500)

        }
        return (<h1 style={{border:"solid black 5px", marginLeft:"100px", marginRight:"100px", textAlign:"center"}}>The experiment is over! Please wait to be redirected...</h1>
        )
    }
}
export default Endscreen